export const photosListReducer = (state= [], action) => {
   switch (action.type) {
      case 'FETCH_PHOTOS':
         return action.payload;
      default:
         return state
   }
}

export const openedPhotoReducer = (state = {statu: false, photo: null}, action) => {
   switch (action.type) {
      case 'OPEN_PHOTO':
         return {...state, ...action.payload};
      case 'CLOSE_PHOTO':
         return {...state, ...action.payload}
      default:
         return state;
   }
}