import React from 'react';
import './Modal.scss';
import { connect } from 'react-redux';
import {closePhoto} from '../actions';

function Modal(props) {

   const renderModal = () => {
      if (props.openedPhoto.statu) {
         return (
            <div className='modal-container' 
            onClick={() => props.closePhoto()}>
               <div className='modal-img' onClick={e => e.stopPropagation()}>
                  <img src={props.openedPhoto.photo.urls.regular} />
               </div>
            </div>
         )
      }
          
      return null;  
   }
   

   return (
      <React.Fragment>
         {renderModal()}
      </React.Fragment>
   )
}

const mapStateToProps = state => {
   return {openedPhoto: state.openedPhoto}
}

export default connect(mapStateToProps, {closePhoto})(Modal);
