import axios from 'axios';

export const fetchPhotos = () => {
   return async dispatch => {
      const response = await axios.get('https://api.unsplash.com/photos/random', {
         params: {count: 12},
         headers: {Authorization: 'Client-ID enJlGjf6NGftgeCT-05BkxfCOTvbpCMecPKUDR2bO8w'}
      })

      dispatch({type: 'FETCH_PHOTOS', payload: response.data})
   }
}

export const openPhoto = (photo) => {
   return {
      type: 'OPEN_PHOTO',
      payload: {
         statu : true,
         photo
      }
   }
}

export const closePhoto = () => {
   return {
      type: 'CLOSE_PHOTO',
      payload: {
         statu : false,
         photo: null
      }
   }
}