import React, { Component } from 'react';
import './Gallery.scss';
import { connect } from 'react-redux';
import {fetchPhotos, openPhoto} from '../actions';


export class Gallery extends Component {

   componentDidMount() {
      this.props.fetchPhotos();
   }
  
   render() {
      return (
         
         <div className='gallery'>
            <div className='gallery-column'>
                  {this.props.photos.map((photo, index) => {
                     if (index <= 4) {
                        return <img src={photo.urls.small} key={index}
                              alt={photo.alt_description}
                              onClick={() => this.props.openPhoto(photo)}/>         
                     }
                     return null;
                  })}

               </div>
               <div className='gallery-column'>
                  {this.props.photos.map((photo, index) => {
                     if (index > 4 && index <= 8) {
                        return <img src={photo.urls.small} key={index}
                                 alt={photo.alt_description}
                                 onClick={() => this.props.openPhoto(photo)}/>
                     }
                     return null;
                  })}
               </div>
               <div className='gallery-column'>
                  {this.props.photos.map((photo, index) => {
                     if (index > 8 && index <= 12) {
                        return <img src={photo.urls.small} key={index} 
                                 alt={photo.alt_description}
                                 onClick={() => this.props.openPhoto(photo)}/>
                     }
                     return null;
                  })}
               </div>
         </div>
      )
   }
}


const mapStateToProps = state => {
   return {
      photos: state.photos
   }
}

export default connect(mapStateToProps, {fetchPhotos, openPhoto})(Gallery)
